@import url("https://fonts.googleapis.com/css2?family=Nunito&display=swap");

*,
*:focus,
*:active,
*:hover,
*:visited,
*:focus-visible,
*:focus-within,
*:focus:not(:focus-visible),
*::before,
*::after {
  /* box-shadow: none;
  outline: 0;
  outline: 0 !important;
  border: 0; 
 box-shadow: 0 0 0 0 rgba(0, 0, 0, 0) !important;
  outline: 0.1vw transparent;
  border: 0.1vw solid transparent; */
  outline: 0;
  border: 0;
  -webkit-box-shadow: none;
          box-shadow: none;
}

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

html {
  font-size: 62.5%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  font-family: "Nunito", sans-serif;
  font-weight: 400;
  height: 100vh;
  color: #333;
  background-image: -webkit-gradient(linear, right bottom, left top, from(#753682), to(#bf2e34));
  background-image: -webkit-linear-gradient(bottom right, #753682 0%, #bf2e34 100%);
  background-image: -o-linear-gradient(bottom right, #753682 0%, #bf2e34 100%);
  background-image: linear-gradient(to top left, #753682 0%, #bf2e34 100%);
  background: -webkit-gradient(linear, right bottom, left top, from(#28b487), to(#7dd56f));
  background: -webkit-linear-gradient(bottom right, #28b487, #7dd56f);
  background: -o-linear-gradient(bottom right, #28b487, #7dd56f);
  background: linear-gradient(to top left, #28b487, #7dd56f);

  display: -ms-flexbox;

  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.id {
  height: 100vh;
}
/* LAYOUT */
main {
  position: relative;
  width: 85rem;
  /* height: 60rem; */
  background-color: rgba(255, 255, 255, 0.35);
  -webkit-backdrop-filter: blur(200px);
          backdrop-filter: blur(200px);
  -webkit-filter: blur();
          filter: blur();
  -webkit-box-shadow: 0 3rem 5rem rgba(0, 0, 0, 0.25);
          box-shadow: 0 3rem 5rem rgba(0, 0, 0, 0.25);
  border-radius: 9px;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
}

.wrapper {
  height: 88vh;
}

.player {
  -ms-flex: 50% 1;
      flex: 50% 1;
  padding: 5rem;
  /* padding: 9rem; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -webkit-transition: all 0.75s;
  -o-transition: all 0.75s;
  transition: all 0.75s;
}

/* ELEMENTS */
.name {
  position: relative;
  font-size: 4rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  word-spacing: 2px;
  font-weight: 300;
  margin-bottom: 1rem;
}
h1.name {
  font-weight: 700;
  text-align: center;
}

.score {
  font-size: 8rem;
  font-weight: 300;
  color: #c7365f;
  margin-bottom: auto;
}

.player--active {
  background-color: rgba(255, 255, 255, 0.4);
}
.player--active .name {
  font-weight: 700;
}
.player--active .score {
  font-weight: 400;
}

.player--active .current {
  opacity: 1;
}

.current {
  background-color: #c7365f;
  background-color: #28b487;
  opacity: 0.8;
  border-radius: 9px;
  color: #fff;
  width: 45%;
  width: 65%;
  padding: 2rem;
  /* padding-bottom: 0; */
  text-align: center;
  -webkit-transition: all 0.75s;
  -o-transition: all 0.75s;
  transition: all 0.75s;
}

.current-label,
label {
  text-transform: uppercase;
  margin-bottom: 1rem;
  font-size: 1.7rem;
  color: #ddd;
}

.current-score {
  font-size: 3.5rem;
}

/* ABSOLUTE POSITIONED ELEMENTS */
.btn {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  color: #444;
  background: none;
  border: none;
  font-family: inherit;
  font-size: 1.8rem;
  text-transform: uppercase;
  cursor: pointer;
  font-weight: 400;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;

  background-color: white;
  background-color: rgba(255, 255, 255, 0.6);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);

  padding: 0.7rem 1.3rem;
  /* padding: 0.7rem 2.5rem; */
  border-radius: 50rem;
  -webkit-box-shadow: 0 1.75rem 3.5rem rgba(0, 0, 0, 0.1);
          box-shadow: 0 1.75rem 3.5rem rgba(0, 0, 0, 0.1);
}

.btn::first-letter {
  font-size: 2.4rem;
  display: inline-block;
  /* margin-right: 0.7rem; */
}

.btn--new {
  top: 0rem;
  /* top: 4rem; */
}
.btn--roll {
  top: 20.3rem;
  /* top: 39.3rem; */
}
.btn--hold {
  top: 29.1rem;
  /* top: 46.1rem; */
}

.btn:active {
  -webkit-transform: translate(-50%, 3px);
      -ms-transform: translate(-50%, 3px);
          transform: translate(-50%, 3px);
  -webkit-box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.15);
          box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.15);
}

.btn:focus {
  outline: none;
}

.dice {
  position: absolute;
  left: 50%;
  top: 10.5rem;
  /* top: 16.5rem; */
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  /* height: 10rem; */
  width: 7%;
  height: 15%;
  -webkit-box-shadow: 0 2rem 5rem rgba(0, 0, 0, 0.2);
          box-shadow: 0 2rem 5rem rgba(0, 0, 0, 0.2);
}

.player--winner {
  background-color: #2f2f2f;
}

.player--winner .name {
  font-weight: 700;
  color: #c7365f;
  font-size: 2.5rem;
}

.hidden {
  /* display: none; */
  visibility: hidden;
}
/* form.hidden {
  /* display: none; */
/* visibility: hidden;
} */

.form {
  text-transform: uppercase;
  margin-bottom: 1rem;
  font-size: 1.7rem;
  background: none;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.form-control {
  display: -ms-flexbox;
  display: flex;
  width: 85%;
  margin: 2rem;
}
.form-control > label {
  padding: 0.2rem 1rem;
  color: #ddd;
  text-align: center;
}
.form button.btn-submit {
  background: #28b487;
  font-size: 1.7rem;
  margin: 0.5rem;
  padding: 0.2rem 1rem;
  opacity: 0.8;
  border-radius: 9px;
  color: #fff;
  width: 20%;
  padding: 2rem;
  text-align: center;
  transition: all 0.75s;
  -webkit-transition: all 0.75s;
  -moz-transition: all 0.75s;
  -ms-transition: all 0.75s;
  -o-transition: all 0.75s;
}
.input {
  height: 3rem;
  width: 90%;
  margin: 0 1rem;
  padding: 1rem;
  font-size: 1.5rem;
}



